.teampage {
  width: 100vw;
  // max-width: 1200px;
  overflow: scroll;
  margin: 0 auto;
  color: #fff;
  .box {
    padding-top: 40px;
    .header {
      /* STRATEGY */

      /* header/header 1 */
      font-family: "Michroma";
      font-style: normal;
      font-weight: 400;
      font-size: 32px;
      line-height: 40px;
      /* identical to box height, or 125% */

      /* gradient.title */
      background: linear-gradient(180deg, #7f8790 0%, #4e5c6a 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;

      text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
    }
    .desc {
      /* stcubed implements a diverse approach, engaging in transactions within both the primary and secondary markets while exploring opportunities both onchain and offchain. */

      width: 512px;
      height: 96px;
      font-family: "Chakra Petch";
      font-style: italic;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      /* or 120% */

      color: #f3f4f5;
    }
  }

  .team {
    padding-top: 40px;
    padding-bottom: 40px;
    .text {
      cursor: pointer;
      font-family: "Chakra Petch";
      font-style: italic;
      font-weight: 400;
      font-size: 20px;
      line-height: 32px;
      text-align: right;
      color: #24dc6e;
    }
  }
  .teamBox {
    // overflow: hidden;

    ul {
      .libox {
        /* Frame 30 */
        margin: 15px 0;
        /* Frame 30 */
        cursor: pointer;
        /* Auto layout */
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-end;
        align-content: center;
        padding: 0px;
        row-gap: 0px;

        width: 100%;
        height: 192px;

        background: #ffb68a;
        /* translucent light border */
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15),
          inset 0px 0px 2px rgba(255, 255, 255, 0.15);
        img {
          /* image */

          /* Auto layout */
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          padding: 0px;
          gap: 10px;

          width: 192px;
          height: 192px;
        }
        .box {
          /* body */

          /* Auto layout */
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          padding: 24px 32px 32px 24px;
          gap: 16px;

          width: calc(100% - 192px);
          min-width: 320px;
          height: 192px;
          min-height: 192px;

          background: #1f2d34;

          /* Inside auto layout */
          flex: none;
          order: 1;
          flex-grow: 1;
          .title {
            /* title */

            width: 648px;
            height: 31px;

            font-family: "Chakra Petch";
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            line-height: 31px;

            color: #7f8790;

            /* Inside auto layout */
            flex: none;
            order: 0;
            align-self: stretch;
            flex-grow: 0;
          }
          .bottom {
            /* content */

            /* Auto layout */
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            padding: 0px;
            font-family: "Chakra Petch";

            width: 648px;
            height: 80px;

            /* Inside auto layout */
            flex: none;
            order: 1;
            align-self: stretch;
            flex-grow: 0;
            li {
              margin-left: 0.6rem;
              list-style-type: disc;
            }
          }
        }
      }
    }
  }
}

@media only screen and (min-width: 822px) {
  .teampage {
    width: 100vw;
    max-width: 1200px;
  }
}
@media only screen and (min-width: 100px) and (max-width: 821px) {
  .teampage {
    width: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 0 40px;
    .box {
      /* Frame 3 */

      /* Auto layout */

      width: 100%;
      // max-width: 512px;

      .header {
        padding-bottom: 30px;
      }
      .desc {
        width: 100%;
        max-width: 512px;
        height: auto;
        padding-bottom: 40px;
      }
    }
    .teamBox {
      ul {
        .libox {
          background-color: transparent;
          &.b1 {
            background-color: rgba(255, 182, 138, 1);
          }
          &.b2 {
            background-color: rgba(255, 129, 197, 1);
          }
          &.b3 {
            background-color: rgba(129, 129, 255, 1);
          }
          &.b4 {
            background-color: rgba(68, 65, 82, 1);
          }
          height: auto;
          .box {
            height: auto;
            width: 100%;
            min-width: 100%;
            .bottom {
              width: 100%;
              height: auto;
            }
          }
        }
      }
    }
    .team {
      padding-bottom: 40px;
    }
  }
}

@primary-color: #467DFD;